import { useState, useEffect } from "react";
import {
    Card,
    Row,
    Col,
    Button,
    Form,
    Select,
    Input,
    List,
    Spin,
    Popconfirm,
    Upload,
    Image,
} from "antd";
import axios from "axios";
import notify from "general/notify";
import { DeleteOutlined, InboxOutlined } from "@ant-design/icons";
import { getNumeric } from "general/Helper";

const { Option } = Select;
const { Dragger } = Upload;

const Images = () => {
    const [loading, setLoading] = useState(true);
    const [typeList, setTypeList] = useState([]);
    const [selectedType, setSelectedType] = useState(null);
    const [imagesList, setImagesList] = useState([]);
    const [imagesListLoading, setImagesListLoading] = useState(false);
    const [selectedImageSrc, setSelectedImageSrc] = useState(null);

    useEffect(() => {
        axios
            .post("IO/GetTypeNames")
            .then(({ data }) => {
                setTypeList(data);
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setLoading(false));
    }, []);

    const onTypeChange = (value = selectedType) => {
        setSelectedType(value);
        setSelectedImageSrc(null);

        setImagesListLoading(true);
        axios
            .post("IO/GetList", { typeName: value })
            .then(({ data }) => {
                setImagesList(data.filter((item) => item.isActive === true));
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setImagesListLoading(false));
    };

    const onSelectImage = (id) => {
        setSelectedImageSrc(null);
        let newImagesList;
        newImagesList = imagesList.map((item) => {
            if (item.id === id) {
                setSelectedImageSrc(item.fileUri);
                return { ...item, selected: true };
            } else {
                return { ...item, selected: false };
            }
        });

        setImagesList(newImagesList);
    };

    const onImageDelete = (id) => {
        setImagesListLoading(true);
        axios
            .post("IO/Modify", { id })
            .then(({ message }) => {
                onTypeChange();
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setImagesListLoading(false));
    };

    const onFinish = (values) => {
        const formData = new FormData();
        formData.append("OrderId", values.OrderId || 1);
        formData.append("TypeName", values.TypeName);
        formData.append("title", values.title || '');
        formData.append("file", values.file.file);

        window.headers = {
            "content-type": "multipart/form-data",
        };

        setLoading(true);
        axios
            .post("IO/Upload", formData)
            .then(({ message }) => {
                onTypeChange();
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setLoading(false));
    };

    const uploadProps = {
        maxCount: 1,
        beforeUpload: () => {
            return false;
        },
    };

    return (
        <Row gutter={16}>
            <Col sm={24} xs={24} md={19} lg={12} xlg={12}>
                <Card title="">
                    <Form
                        labelCol={{ xs: 7, md: 6, lg: 6, xlg: 5 }}
                        onFinish={onFinish}
                        scrollToFirstError={true}
                    >
                        <Form.Item name="title" label="">
                            <Input />
                        </Form.Item>

                        <Form.Item
                            name="TypeName"
                            label=" "
                            rules={[{ required: true }]}
                        >
                            <Select onChange={onTypeChange}>
                                {typeList.map((item) => (
                                    <Option value={item}>{item}</Option>
                                ))}
                            </Select>
                        </Form.Item>
                        {selectedType === "SPLASH" && (
                            <Form.Item
                                name="OrderId"
                                label=""
                                rules={[{ required: true }]}
                                normalize={(val) => getNumeric(val)}
                            >
                                <Input className="ltr" maxLength={2} />
                            </Form.Item>
                        )}

                        <Form.Item
                            name="file"
                            label=""
                            rules={[{ required: true }]}
                        >
                            <Dragger {...uploadProps}>
                                <p className="ant-upload-drag-icon">
                                    <InboxOutlined />
                                </p>
                                <p className="ant-upload-hint">
                                         
                                </p>
                            </Dragger>
                        </Form.Item>
                        <div className="ant-card-footer">
                            <Form.Item>
                                <Button
                                    type="primary"
                                    htmlType="submit"
                                    className="wide-button"
                                >
                                    
                                </Button>
                            </Form.Item>
                        </div>
                    </Form>
                </Card>
            </Col>

            <Col sm={24} xs={24} md={19} lg={12} xlg={12}>
                <Card title="">
                    <Spin delay={900} spinning={imagesListLoading}>
                        <List
                            className="image-list"
                            dataSource={imagesList}
                            renderItem={({
                                fileUri,
                                title,
                                id,
                                orderId,
                                typeName,
                                selected = false,
                            }) => (
                                <List.Item
                                    className={selected && "selected"}
                                    onClick={() => onSelectImage(id)}
                                    key={id}
                                    actions={[
                                        <div className="list-action-left">
                                            <Popconfirm
                                                title="  "
                                                onConfirm={() =>
                                                    onImageDelete(id)
                                                }
                                            >
                                                <DeleteOutlined />
                                            </Popconfirm>
                                        </div>,
                                    ]}
                                >
                                    {typeName !== "SPLASH"
                                        ? title
                                            ? title
                                            : " "
                                        : title
                                        ? title + `( : ${orderId})`
                                        : `  ( : ${orderId})`}
                                </List.Item>
                            )}
                        />
                        <div className="image-show">
                            <Image
                                src={selectedImageSrc}
                                fallback="data:image/png;base64,"
                            />
                        </div>
                    </Spin>
                </Card>
            </Col>
        </Row>
    );
};

export default Images;
