import { useState, useEffect } from 'react';
import persian from 'react-date-object/calendars/persian';
import persian_fa from 'react-date-object/locales/persian_fa';
import { Calendar } from 'react-multi-date-picker';
import { Card, Col, Table, Spin } from 'antd';
import notify from 'general/notify';
import axios from 'axios';
import { getDateFromObject } from 'general/Helper';
import { CloseOutlined, CheckOutlined } from '@ant-design/icons';

const weekDays = ['', '', '', '', '', '', ''];

const ShowCalendar = ({ match }) => {
    const [date, setDate] = useState(null);
    const [loading, setLoading] = useState(false);
    const [selectedDayProgramList, setSelectedDayProgramList] = useState([]);
    const [welfareId, setWelfareId] = useState(null);
    const [holidayList, setHolidayList] = useState([]);
    const [holidaysList, setHolidaysList] = useState([]);
    const { url } = match;

    useEffect(() => {
        setDate(null);
        const type = url.split('/')[1]?.toUpperCase();
        axios.post('Welfare/Get', { type }).then(({ data }) => {
            setWelfareId(data[0].id);
        });

        axios
            .post('Calendar/Get', {
                shamsiDate: '0',
                description: 'all',
            })
            .then(({ data }) => {
                setHolidayList(data);
                const holidaysList = data.map((item) => item.shamsiDate);
                setHolidaysList(holidaysList);
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setLoading(false));
    }, [url]);

    const onDayChange = (date, isObject = false) => {
        let selectedDate;
        if (!isObject) {
            selectedDate = getDateFromObject(date);
            setDate(selectedDate);
        } else {
            selectedDate = date;
        }

        setLoading(true);
        axios
            .post('DailyProgram/Get', {
                dateFrom: selectedDate,
                dateTo: selectedDate,
                welfare: welfareId,
            })
            .then(({ data }) => {
                setSelectedDayProgramList(data);
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setLoading(false));
    };

    const columns = [
        {
            title: '',
            dataIndex: 'capacity',
            key: 'capacity',
        },
        {
            title: '  ',
            key: 'isVip',
            render: (record) =>
                record.isVip ? (
                    <CheckOutlined className="green" />
                ) : (
                    <CloseOutlined className="red" />
                ),
        },
        {
            title: ' ',
            key: 'id',
            render: ({ startTime, endTime }) => ` ${startTime}  ${endTime}`,
        },
        {
            title: '',
            dataIndex: 'genderAcceptance',
            key: 'genderAcceptance',
        },
    ];

    return (
        <Col sm={24} xs={24} md={19} lg={15} xlg={12}>
            <Spin delay={900} spinning={loading}>
                <Card title=" ">
                    <div id="large-calendar">
                        <Calendar
                            calendar={persian}
                            locale={persian_fa}
                            calendarPosition="bottom-right"
                            weekDays={weekDays}
                            onChange={onDayChange}
                            value={date}
                            mapDays={({ date }) => {
                                let props = {};
                                let isWeekend =
                                    date.weekDay.index === 6 ||
                                    date.weekDay.index === 5;

                                if (
                                    holidaysList.includes(
                                        getDateFromObject(date)
                                    ) ||
                                    isWeekend
                                )
                                    return {
                                        disabled: true,
                                        style: { color: '#f30' },
                                        onClick: () =>
                                            notify.error(
                                                '     '
                                            ),
                                    };
                            }}
                        />
                    </div>
                    <br />
                    <Table
                        bordered
                        columns={columns}
                        pagination={{
                            hideOnSinglePage: true,
                        }}
                        dataSource={selectedDayProgramList}
                        loading={loading}
                        scroll={{ x: true }}
                    />
                </Card>
            </Spin>
        </Col>
    );
};

export default ShowCalendar;
