import React, { useState, useEffect } from 'react';
import {
    Row,
    Card,
    Col,
    Form,
    Input,
    Spin,
    Button,
    Table,
    Select,
    Modal,
} from 'antd';
import notify from 'general/notify';
import axios from 'axios';
import DatePicker from 'react-multi-date-picker';
import TimePicker from 'react-multi-date-picker/plugins/time_picker';
import persian from 'react-date-object/calendars/persian';
import persian_fa from 'react-date-object/locales/persian_fa';
import { getDateFromObject } from 'general/Helper';
import {
    CloseOutlined,
    CheckOutlined,
    PicRightOutlined,
} from '@ant-design/icons';
import { usePaginate } from 'hooks/usePaginate';
import ShowReservationServices from 'components/common/reservation/ShowReservationServices';

const weekDays = ['', '', '', '', '', '', ''];

const { Option } = Select;

const ShowReservationReport = () => {
    const [filterPartloading, setFilterPartloading] = useState(true);
    const [tableLoading, setTableLoading] = useState(false);
    const [welfareList, setWelfareList] = useState([]);
    const [workGroupList, setWorkGroupList] = useState([]);
    const [reservationList, setReservationList] = useState([]);
    const [isFirstRequestSent, setIsFirstRequestSent] = useState(false);
    const [formOutput, setFormOutput] = useState({});
    const [reservationId, setReservationId] = useState(null);
    const [isServicesModalVisible, setIsServicesModalVisible] = useState(false);
    const [form] = Form.useForm();

    useEffect(() => {
        axios
            .all([
                axios.post('Welfare/Get', { type: null }),
                axios.post('Workgroup/Get'),
            ])
            .then((res) => {
                setWelfareList(res[0].data);
                setWorkGroupList(res[1].data);
                setFilterPartloading(false);
            });
    }, []);

    const columns = [
        {
            title: '',
            dataIndex: 'namefa',
            key: 'namefa',
        },
        {
            title: ' ',
            dataIndex: 'personelCode',
            key: 'personelCode',
        },
        {
            title: ' ',
            dataIndex: 'mobile',
            key: 'mobile',
        },
        {
            title: '',
            dataIndex: 'welfarenamefa',
            key: 'welfarenamefa',
        },
        {
            title: '',
            dataIndex: 'dailyProgramShamsiDate',
            key: 'dailyProgramShamsiDate',
        },
        {
            title: ' ',
            key: 'startTime',
            render: ({ startTime, endTime }) => `${endTime} - ${startTime}`,
        },
        {
            title: '',
            dataIndex: 'statusReserve',
            key: 'statusReserve',
        },
        {
            title: '  ',
            key: 'isVip',
            className: 'edit',
            render: (record) =>
                record.isVip ? (
                    <CheckOutlined className="green" />
                ) : (
                    <CloseOutlined className="red" />
                ),
        },
        {
            title: ' ',
            onCell: ({ reservationId }) => {
                return {
                    onClick: () => {
                        setReservationId(reservationId);
                        setIsServicesModalVisible(true);
                    },
                };
            },
            key: 'reservationId',
            className: 'act-icon edit',
            render: () => <PicRightOutlined style={{ fontSize: 18 }} />,
        },
    ];

    const {
        pageIndex,
        setPageIndex,
        pageSize,
        setPageSize,
        total,
        setTotal,
        paginationData,
    } = usePaginate(form.submit);

    const onFinish = (values) => {
        values.dateFrom = getDateFromObject(values.dateFrom);

        if (values.dateTo) {
            values.dateTo = getDateFromObject(values.dateTo);
            if (values.dateTo < values.dateFrom) {
                notify.error('       ');
                return;
            }
        }

        setFormOutput(values);

        setTableLoading(true);

        axios
            .post('Report/Reservation', values)
            .then(({ data }) => {
                setReservationList(data);
                if (isFirstRequestSent === false) {
                    setIsFirstRequestSent(true);
                }
            })
            .catch((errorMessage) => notify.error(errorMessage))
            .then(() => setTableLoading(false));
    };

    const downloadReport = () => {
        // axios
        //     .post("Report/ReservationExcel", formOutput, {
        //         headers: {
        //             "Content-Disposition": "attachment; filename=template.xlsx",
        //             "Content-Type":
        //                 "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        //         },
        //         responseType: "arraybuffer",
        //     })
        //     .then((response) => {
        //         const url = window.URL.createObjectURL(
        //             new Blob([response.data])
        //         );
        //         const link = document.createElement("a");
        //         link.href = url;
        //         link.setAttribute("download", "template.xlsx");
        //         document.body.appendChild(link);
        //         link.click();
        //     })
        //     .catch((error) => console.log(error));
        const instance = axios.create({
            responseType: 'arraybuffer',
        });

        instance
            .post('Report/ReservationExcel', formOutput)
            .then((response) => {
                const fileName = `${formOutput.dateFrom}-${formOutput.dateTo}`;
                const url = window.URL.createObjectURL(
                    new Blob([response.data])
                );
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', `${fileName}.xls`);
                document.body.appendChild(link);
                link.click();
            })
            .catch((error) => console.log(error));
    };

    return (
        <div>
            <Card>
                <Spin delay={900} spinning={filterPartloading}>
                    <Form
                        labelCol={{ xs: 3, md: 5, lg: 5 }}
                        onFinish={onFinish}
                        scrollToFirstError={true}
                        form={form}
                        initialValues={{
                            welfareId: 0,
                            presenceStatus: -1,
                            workgroupId: 0,
                        }}
                    >
                        <Row gutter={18}>
                            <Col lg={7} md={12} xs={24}>
                                <Form.Item
                                    name="dateFrom"
                                    label=" "
                                    rules={[{ required: true }]}
                                >
                                    <DatePicker
                                        format="YYYY/MM/DD"
                                        calendar={persian}
                                        locale={persian_fa}
                                        weekDays={weekDays}
                                    />
                                </Form.Item>

                                <Form.Item
                                    name="dateTo"
                                    label=" "
                                    // rules={[{ required: true }]}
                                >
                                    <DatePicker
                                        format="YYYY/MM/DD"
                                        calendar={persian}
                                        locale={persian_fa}
                                        weekDays={weekDays}
                                    />
                                </Form.Item>
                            </Col>

                            <Col lg={8} md={12} xs={24}>
                                <Form.Item name="welfareId" label="">
                                    <Select>
                                        <Option value={0}></Option>
                                        {welfareList.map(({ id, typename }) => (
                                            <Option value={id}>
                                                {typename}
                                            </Option>
                                        ))}
                                    </Select>
                                </Form.Item>

                                <Form.Item
                                    name="presenceStatus"
                                    label=""
                                    // rules={[{ required: true }]}
                                >
                                    <Select>
                                        <Option value={-1}></Option>
                                        <Option value={0}>
                                               
                                        </Option>
                                        <Option value={1}> </Option>
                                        <Option value={2}></Option>
                                        <Option value={3}></Option>
                                        <Option value={7} disabled>
                                              
                                        </Option>
                                        <Option value={8}> </Option>
                                        <Option value={9} disabled>
                                              
                                        </Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col lg={{ span: 8, offset: 1 }} md={12} xs={24}>
                                <Form.Item name="workgroupId" label="">
                                    <Select>
                                        <Option value={0}></Option>
                                        {workGroupList.map(({ id, nameFa }) => (
                                            <Option value={id}>{nameFa}</Option>
                                        ))}
                                    </Select>
                                </Form.Item>
                                <Form.Item
                                    wrapperCol={{
                                        lg: { span: 19, offset: 5 },
                                        md: { span: 19, offset: 5 },
                                        sm: { span: 21, offset: 3 },
                                    }}
                                >
                                    <Button
                                        type="primary"
                                        htmlType="submit"
                                        className="wide-button float-left"
                                        block
                                    >
                                        
                                    </Button>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Card>
            {isFirstRequestSent && (
                <Card
                    title=" "
                    extra={[
                        <Button size="small" onClick={downloadReport}>
                             
                        </Button>,
                    ]}
                >
                    <Table
                        bordered
                        columns={columns}
                        pagination={paginationData}
                        loading={tableLoading}
                        dataSource={reservationList}
                        scroll={{ x: true }}
                    />
                </Card>
            )}
            <Modal
                title="  "
                visible={isServicesModalVisible}
                footer={null}
                destroyOnClose={true}
                onCancel={() => setIsServicesModalVisible(false)}
            >
                <ShowReservationServices reservationId={reservationId} />
            </Modal>
        </div>
    );
};

export default ShowReservationReport;
